#md-indicator {
	display: none;
}

#mobile-indicator {
	display: none;
}

@media (min-width:768px) {

	.navbar-wrapper {
		display: block !important;
		opacity: 1 !important;
	}

}

// lg

@media (max-width: 1421px) {

	.top-register-col-1,
	.top-register-col-3 {
		flex: 0 0 25%;
		max-width: 25%;
	}

	.top-register-col-2 {
		flex: 0 0 50%;
		max-width: 50%;
	}

	.top-register-pic img {
		width: 320px !important;
	}

	.top-register-pic-1 img {
		margin: 0 0 0 -150px;
	}

}

// lg END

// md

@media (max-width: 1059px) {

	#md-indicator {
		display: block;
	}

	h1, .h1 {
		font-size: 32px;
		line-height: 40px;
		margin: 0 0 22px 0;
	}

	.header-sub-ttl {
		font-size: 20px;
		line-height: 30px;
	}

	.top-register-col-1,
	.top-register-col-3 {
		display: none;
	}

	.top-register-col-2 {
		flex: 0 0 470px;
		max-width: 470px;
		margin: 0 auto;
	}

	.section-top {
		padding-bottom: 166px;
	}

	.section-top:before {
		bottom: 997px;
	}

	.section-top:after {
		width: 1995px;
		height: 1995px;
	}

	.section-header h2,
	.section-header .h2 {
		font-size: 32px;
		line-height: 40px;
	}

	.section-champ {
		padding-top: 504px;
		padding-bottom: 180px;
	}

	.section-champ:before {
		bottom: 997px;
	}

	.section-champ:after {
		width: 1995px;
		height: 1995px;
	}

	.champ-wrapper {
		margin: 64px 0 -60px 0;
	}

	.champ-block-descr {
		max-width: 320px;
	}

	.champ-block {
		font-size: 16px;
		line-height: 24px;
		margin: 0 0 60px 0;
	}

	.champ-block h3, .champ-block .h3 {
		font-size: 24px;
		line-height: 32px;
	}

	.section-partners {
		padding-top: 480px;
	}

	.section-header {
		margin: 0 0 48px 0;
	}

	.section-start {

		margin: 48px 0 0 0;
		padding-top: 144px;
		padding-bottom: 620px;
	}

	.section-start:before {
		top: 997px;
	}

	.section-start:after {
		width: 1995px;
		height: 1995px;
	}

	.start-wrapper {
		margin: 156px 0 0 0;
	}

}

// md END

// sm

@media (max-width: 767px) {

	#mobile-indicator {
		display: block;
	}

	.container {
		padding: 0 24px;
	}

	.header-col-2,
	.header-col-3 {
		display: none;
	}

	.header-logo-text {
		font-size: 20px;
		line-height: 28px;
	}

	.section-top {
		padding-top: 111px;
		padding-bottom: 139px;
	}

	.section-top:before {
		bottom: 503px;
	}

	.section-top:after {
		width: 1007px;
		height: 1007px;
	}

	.header-sub-ttl {
		font-size: 20px;
		line-height: 28px;
		margin: 0 auto;
		max-width: 300px;
	}

	h1,
	.h1 {
		margin: 0 0 16px 0;
	}

	.top-register-col-2 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.top-register-form {
		width: 366px;
		height: 366px;
		padding: 29px 35px 29px 35px;
	}

	.top-register-form:before {
		transform: translate(-50%, -50%) scale(0.9);
	}

	.top-register-form h3, .top-register-form .h3 {
		font-size: 20px;
		line-height: 28px;
	}

	.champ-block-col-1 {
		order: 1 !important;
		flex: 0 0 100%;
		max-width: 100%;
	}

	.champ-block-col-2 {
		order: 2 !important;
		flex: 0 0 100%;
		max-width: 100%;
	}

	.champ-block {
		font-size: 20px;
		line-height: 32px;
	}

	.champ-block-pic {
		max-width: 100%;
		margin: 0 0 36px 0;
	}

	.champ-block-descr {
		max-width: 100%;
		text-align: center;
	}

	.section-champ:before {
		bottom: 503px;
	}

	.section-champ:after {
		width: 1007px;
		height: 1007px;
	}

	.section-start:before {
		top: 503px;
	}

	.section-start:after {
		width: 1007px;
		height: 1007px;
	}

	.section-champ {
		padding-bottom: 127px;
	}

	.section-partners .section-header {
		max-width: 300px;
		margin-left: auto;
		margin-right: auto;
	}

	.section-start {
		padding-top: 105px;
		padding-bottom: 563px;
	}

	.start-wrapper {
		margin: 94px 0 0 0;
	}

	.start-form {
		padding: 43px 35px 29px 35px;
	}

	footer {
		padding-bottom: 63px;
	}

	.footer-links-item {
		flex: 0 0 100%;
		max-width: 100%;
		display: flex;
		justify-content: center;
		margin-bottom: 33px;
	}

	.footer-links-item:last-child {
		margin: 0;
	}

}

// sm END
