.marquee-list {

}

.marquee-row {
	display: flex;
	flex-wrap: nowrap;
}

.marquee-item {
	padding: 0 53px;
}

.marquee-tmb {
	display: block;
	padding: 17px 0 0 70px;
	position: relative;
	border: none !important;
	color: #000 !important;
	transition: .2em;
}

.marquee-tmb:hover {

}

.marquee-tmb-pic {
	width: 173px;
	height: 83px;
	border-radius: 3px;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	position: absolute;
	left: 0;
	top: 0;
	filter: grayscale(1);
	opacity: .1;
}

.marquee-tmb-inner {
	font-family: 'DINPro';
	font-weight: 500;
	font-size: 24px;
	line-height: 30px;
	letter-spacing: 3px;
	text-transform: uppercase;
	position: relative;
	display: flex;
	white-space: nowrap;
	padding: 0 0 9px 0;
}

.marquee-tmb-inner:after {
	content: "";
	position: absolute;
	top: 100%;
	height: 4px;
	left: 0;
	width: 100%;
	transition: all .35s ease;
}

.marquee-tmb:hover .marquee-tmb-inner:after {
	width: 143px;
}

.marquee-tmb-date {
	position: relative;
	padding: 0 32px 0 0;
	margin: 0 12px 0 0;
}

.marquee-tmb-date:before {
	content:"";
	width: 22px;
	height: 12px;
	background: url(../images/rarr.svg) 0 0 no-repeat;
	position: absolute;
	right: 0;
	top: 11px;
	transition: all .2s ease;
	opacity: 1;
}

.marquee-tmb-date:after {
	content:"";
	width: 22px;
	height: 12px;
	background: url(../images/rarr-hover.svg) 0 0 no-repeat;
	position: absolute;
	right: 0;
	top: 11px;
	transition: all .2s ease;
	opacity: 0;
}

.marquee-tmb:hover .marquee-tmb-date:before {
	opacity: 0;
}

.marquee-tmb:hover .marquee-tmb-date:after {
	opacity: 1;
}

.marquee {
	margin: 0 -53px;
	white-space: nowrap;
	box-sizing: border-box;
	position: relative;
}

.marquee-content {
	width: 300%;
	display: flex;
	animation: marquee 10s linear infinite forwards;
}
.marquee-content:hover {
	animation-play-state: paused;
}

.marquee-list {

}

@keyframes marquee {
	0% {
		transform: translateX(0);
	}
	100% {
		transform: translateX(-66.6%);
	}
}

